<script>

import ContactForm from "./../components/ContactForm.vue";

export default {
    name : 'Contact',

    created(){
        this.$store.dispatch("initMsg");
    },

    computed: {
        getMsg(){
            return this.$store.getters.getMessage;
        },
    },

    methods: {
        updateStateNotifyMsgToUser(){
            this.$store.dispatch("updateStateNotifyMsgToUser");
        },

        sendMsg: function (event){
            const msg_schema = {
                name: event.name,
                phone: event.phone,
                msg: event.msg,
                date: new Date().toDateString()
            };
            this.$store.dispatch("sendMsg", msg_schema);
        },
    },

    components: { ContactForm },
};
</script>


