<template>
    <div class="container mt-5 rounded ">

        <!--jquery img-->
        <img src="../assets/imgs/langs/jquery.png" class="jquery-img mt-3"/>

        <h3 class="text-center pt-4 text-white mt-3">My Tools</h3>
        <div class="shine-hr mt-3"></div>

        <!--if error exist-->
        <div v-if="getMyToolsApiErrStatus">
            <loading-fail redirect="/"/>
        </div>

        <!--check pending status-->
        <div v-else-if="getMyToolsApiPendingStatus" class="mt-5">
            <Pending/>
        </div>

        <!-- loop lnags-->
        <div class="row no-gutters pl-md-3 pr-md-3 mt-3">
            <div v-for="lang in getLangs" :key="lang.key" class="col-md-3 col-6 text-light text-center mt-3 mt-md-4 pl-md-5 pr-md-5 hover pr-1" id="mouseMoveAni" data-value="5" data-aos="fade-in" >
                <div class="bg-secondary rounded p-2"><strong>{{lang.lang}}</strong></div>
            </div>
        </div>

    </div>
</template>


<script>
    import LoadingFail from "./../components/LoadingFail.vue";
    import Pending from "./../components/Pending.vue";
    export default {
        name: "My-Too",

        components:{
            Pending,LoadingFail
        },

        created(){
            //inti firebase api
            this.$store.dispatch('initMyToolsApi');
        },

        computed :{
            // get langs from vuex
            getLangs(){
                let langs = this.$store.getters.getLangs;
                //console.log(langs);
                //return sorted array by priority number
                return langs.sort((a, b) => a.priority - b.priority );
            },

            // get error status
            getMyToolsApiErrStatus(){
                return this.$store.getters.getMyToolsApiErrStatus
            },

            // get pending status
            getMyToolsApiPendingStatus(){
                return this.$store.getters.getMyToolsApiPendingStatus
            }
        },
    }
</script>

<style scoped>
    .jquery-img {
        transform: rotate(20deg);
        width: 45px;
        position: relative;
        left: 80%;
    }
</style>