<template>
    <div class="container pb-5">
        <div class="pl-md-5 pr-md-5">

            <h5 class='text-center text-pink mt-5 pb-1'>Github Repositories</h5>
            <div class="shine-hr"></div>

            <!-- fetching error -->
            <div v-if="getGithubApiErrStatus">
                <LoadingFail redirect='githubapi' />
            </div>
            <!-- Pending -->
            <div v-else-if="getGithubApiPendingStatus" class="mt-5">
                <Pending />
            </div>

            <!--Public Repositories & showing Repositories-->
            <div v-if="getRepos.length > 1" class='text-info mt-4 pl-md-5 pl-2'>
                <span>Public Repositories</span>
                <button class="btn btn-info ml-1 btn-sm pt-0 pb-0 pub-repo">{{getRepos.length}}</button>
                <div class="hide_pc mt-2"> </div>
                <span class="ml-md-4 mt-1">Showing Repositories</span>
                <button class="btn btn-info ml-1 btn-sm pt-0 pb-0 pub-repo">{{getRepos.length - 1}}</button>
            </div>

            <!--Repositories-->
            <div class="card-columns  p-md-4 p-2 mt-2 pt-0 mt-0">

                <div v-for="repo in getRepos" :key="repo.id" id="mouseMoveAni"  data-value="5">

                    <div v-if="repo.name != 'mine' && repo.name != 'me'">

                        <div class="card mt-2 bg-secondary">

                        <div class="card-header p-1 bg-secondary border-0 ">
                            <h6 class="card-title pl-3 pt-2">
                                <a :href="repo.html_url" style="text-decoration: none;font-size: 13px;" class="text-pink">
                                    {{repo.name}}
                                </a>
                            </h6>
                        </div>

                        <div class="card-footer border-0">
                            <div class="card-text">
                                <small class="text-pink" style="font-size: 11px;">{{repo.language}}</small>
                                <a href="https://github.com/ShineShineDev" class="text-pink">
                                    <img  src="../assets/imgs/me/p-4.jpg" class="profile-img float-right ic" alt="">
                                    <small class="float-right mr-2 icons-aling" style="font-size: 11px;">{{repo.owner.login}}</small>
                                </a>
                            </div>
                        </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>

    import LoadingFail from "./../components/LoadingFail.vue";
    import Pending from "./../components/Pending.vue";

    export default {
        name : 'Repositories',

        components:{
            Pending,LoadingFail
        },

        created(){
            this.$store.dispatch('initGithubApi');
        },

        computed: {
            getGithubApiErrStatus(){
              return this.$store.getters.getGithubApiErrStatus;
            },

            getGithubApiPendingStatus(){
                return this.$store.getters.getGithubApiPendingStatus;
            },

            getRepos() {
                return this.$store.getters.getRepos;
            },

        },

    }
</script>

<style scoped>

    .profile-img{
        width: 30px;
        height: 30px;
        border-radius: 100%;
    }

    .pub-repo{
        border-radius: 100%;
    }

    .text-pink{
        color: pink;
    }
</style>
